import axios from 'axios';
import { useState, useEffect } from 'react';

function Show() {
  const [res, setRes] = useState([]);

  useEffect(() => {
    axios.get('http://localhost:8081/showall')
      .then((response) => {
        console.log(response.data);
        setRes(response.data);
      })
      .catch((error) => {
        console.error('Error fetching data:', error);
      });
  }, []);

  // Define a function to map role numbers to role names,
  // handling unknown roles by returning their original values
  const mapRoleName = (roleNumber) => {
    console.log("Role number:", roleNumber);
    switch (roleNumber) {
      case 1:
        return 'Admin';
      case 2:
        return 'Counsellor';
      case 3:
        return 'Visitor';
      default:
        // Return the original role number if not mapped
        return roleNumber;
    }
  };

  // **Directly modify the data within the map function to display role names**
  return (
    <div>
      <h1>Table of Data</h1>
      <table style={{ borderCollapse: 'collapse', width: '100%' }}>
        <thead>
          <tr style={{ border: '1px solid black' }}>
            <th style={{ border: '1px solid black', padding: '8px' }}>email</th>
            <th style={{ border: '1px solid black', padding: '8px' }}>Name</th>
            <th style={{ border: '1px solid black', padding: '8px' }}>password</th>
            <th style={{ border: '1px solid black', padding: '8px' }}>role</th>
            {/* Add more headers here based on your data structure */}
          </tr>
        </thead>
        <tbody>
          {res.map((item) => {
            // Modify the 'role' property with the mapped role name
            item.role = mapRoleName(item.role);
            return (
              <tr key={item.id} style={{ border: '1px solid black' }}>
                <td style={{ border: '1px solid black', padding: '8px' }}>{item.email}</td>
                <td style={{ border: '1px solid black', padding: '8px' }}>{item.name}</td>
                <td style={{ border: '1px solid black', padding: '8px' }}>{item.password}</td>
                <td style={{ border: '1px solid black', padding: '8px' }}>{item.role}</td>
                {/* Add more columns here based on your data structure */}
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
}

export default Show;
